        <!-- <div class="webblle">
          <OptionSwitch @onChange="coinHandleChange" :title="language.well_type" :tabs="coinsTabs" />
        </div> -->

      <div class="a-option-content" >
      <OptionHeader :title="language.start_earning" style=" margin-top: 0px">
           {{language.trading}}
      </OptionHeader>
      </div>

    <div class="a-option-content flex flex-wrap jc-between">
      <m-box class="mt-22 a-option-left">
        <h2 class="fs-22 text lh-1 pm-0">
            {{language.liquidity}}
