            <span v-else> ({{language.down}}) </span>
          </h2>
         <div class="box-pool">
         <div class="left-flex">
                 <div class="flex-price">
                      <OptionInputAmount  v-model="amount" :symbol="currentCoin" :balance="balanceFormatter(currentBalance)" :title="language.Amount" />
                 </div>

                  <div class="tips-box" v-if="isPhone">
                      {{language.wallet_balance}} 
                      <span @click="hanldeInpputBalance" class="cursor-p">{{balanceFormatter(currentBalance)}}</span>
                      {{symbol1 || symbol}}
