           </div>

           <div class="balance-box">
              <div class="div1">
                    <LiquiditySlider  :totalBalance="options[contractName].totalBalance" :amount="amount" :coin="currentCoin" />
                   
                    <m-switch v-model="hedged" style="margin-top:20px">
