                    </m-switch>

                    <div class="mt-16" style="margin-top:20px">
                            {{language.high_risk}}
                    </div>

                    <OptionSwitch style="margin-top:10px" colorBtn @onChange="typeHandleChange" :title="language.pool_type" :tabs="poolTypeTabs" />
                
                    <m-button style="margin-top:30px;width: 100%;" :loading="loading" @click="approveHandle(contractName)" v-show="!isApprove"  class="a-option-trade hdr-el-blue flex1" :height="36" >
                         {{language.enable}}  {{currentCoin}}
                    </m-button>

                    <m-button style="margin-top:30px;width: 100%;" :loading="loading" @click="provideFromDefxCall" v-show="isApprove" class="a-option-trade hdr-el-blue flex1" :height="36" >
                              {{language.confirm}}
                    </m-button>
               </div>


              <div class="div2">
                  <m-input-plane class="option-tips" >
                        {{t('aoption_liquidity_submit_tips', {
                        amount,
