                        type: type === OptionType.UP ? language.aoption_look_up : language.aoption_look_down,
                        apy:Apy || 0,
                        })}}
                          
                        <span v-show="hedged">{{language.aoption_liquidity_submit_tips1}}</span>
                        {{t(language.aoption_liquidity_submit_tips2, {
