<script>
 import devise from "./components/devise.vue";

import {mapState, mapActions, mapGetters} from 'vuex';

import {balanceFormatter} from "@/common/dataConvertor";
import { encodeDefxCallName, maxUint256, BN, OptionType, OptionCoin } from "@/common/util";
import mBox from "@/components/mBox";
import mButton from "@/components/mButton";
import MSwitch from "@/components/mSwitch";
import mInputPlane from "@/components/mInputPlane";
import OptionHeader from "./components/OptionHeader";
import OptionSwitch from "./components/OptionSwitch";
import OptionInputAmount from "./components/OptionInputAmount";
import LiquidityRightTop from "./components/LiquidityRightTop";
import LiquiditySlider from "./components/LiquiditySlider";
import {coinsTabs} from './datas'

import lookUp from '@/images/option/look-up.png'
import lookDown from '@/images/option/look-down.png'

const poolTypeTabs = [
  { lable: "up", type: 'up', color: 'green', border: 'border-green hdr-el-green', image: lookUp  },
  { lable: "down", type: 'down', color: 'red', border: 'border-red hdr-el-red', image: lookDown },
]

export default {
  name: "americanOptionIndex",
  components: {OptionHeader,devise, mBox, mButton, OptionSwitch, OptionInputAmount, mInputPlane, LiquidityRightTop, LiquiditySlider, MSwitch},
  computed:{
    ...mapGetters(['language', 't']),
    ...mapState('app', ['balance']),
    ...mapState('aoption', ['options']),
    contractName() {
      return encodeDefxCallName(this.type, this.coin)
    },
    isApprove() {
      return this.options[this.contractName].allowance.gt(0)
    },
    unlockDate() {
      if (this.hedged) {
        return Number(this.options[this.contractName].lockupPeriodForHedgedTranches) * 1000
      }
      return Number(this.options[this.contractName].lockupPeriodForUnhedgedTranches) * 1000
    },
    displayDate() {
      return new Date(new Date().getTime() + this.unlockDate).format('yyyy-MM-dd hh:mm')
    },
    currentCoin() {
      return this.type === OptionType.BASE_TYPE ?  this.coin : OptionCoin.BASE_SYMBOL ;
    },
    currentBalance() {
      return this.balance[this.currentCoin]
    },
    Apy() {
      if(this.item) {
        let numApy = (this.item.withdrawAmount - this.item.amount)*100* 365*60*60*24/(this.item.endtime-this.item.timestamp) / this.item.amount  
        let Apy = numApy.toFixed(2) 
        return Apy;
      }
      return 0;
    },
    profit() {
      if(this.item) {
        let numApy = (this.item.withdrawAmount - this.item.amount)* this.unlockDate * 100 /1000/(this.item.endtime-this.item.timestamp) / this.item.amount  
        let Apy = numApy.toFixed(2) 
        return Apy;
      }
      return 0;
    }
  },
  data(){
    return {
      isPhone:false,
      OptionType,
      hedged:false, // 是否对冲
      coin: coinsTabs[0].symbol,
      type: poolTypeTabs[0].type,
      poolLength:0,
      loading:false,
      amount: 0.01,
      slider: 12,
      calcSlider: 12,
      poolTypeTabs,
      coinsTabs,
      item: null
    }
  },
  methods:{
    balanceFormatter,
    ...mapActions('aoption', ['setOption']),
    // getPoolList(){
    //   this.loading = true;
    //   this.$api.getPoolLength().then(length=>{
    //     this.poolLength = Number(length);
    //     setTimeout(()=>this.loading = false,500);
    //   }).catch(()=>this.loading = false);
    // },
    coinHandleChange(item) {
      this.coin = item.symbol
      console.log(this.coin)
    },
   
    getActivePoolInfo(contractName) {
      this.$api.getDefxCallData(contractName).then(info => {
        this.setOption({key: contractName, info});
      })
    },
    provideFromDefxCall() {
      if (!Number(this.amount)) {
        this.$toast.warning(this.language.amount_Please)
        return
      }
      if (this.currentBalance.lt(new BN(this.amount).toWei('balance'))) {
        this.$toast.warning(this.language.insufficient_balance)
        return
      }
      this.loading = true
      this.$api.provideFromDefxCall(this.contractName, this.amount, this.hedged).then(() => {
       
        this.$toast.success(this.language.added_successfully)
        this.$api.getBalance();
      }).catch((err) => {  
        if (err?.code !== 4001) {
          this.$toast.error(this.language.add_failed)
        }
      }).finally(() => {
        this.loading = false
      })
    },
    approveHandle(contractName) {
      this.loading = true
      this.$api.erc20ApproveDefxCall(contractName).then(() => {
        this.setOption({key: contractName, info: { allowance: maxUint256 }});
        this.$toast.success(this.language.enabled_successfully)
      }).catch(() => {  
        this.$toast.error(this.language.failed_to_enable)
      }).finally(() => {
        this.loading = false
      })
    },
     hanldeInpputBalance() {
      if (this.disableBalace) return
      this.currentValue = this.balance
    },
     typeHandleChange(item) {
      this.type = item.type
      this.getLastHistorytranches()
    },
    getLastHistorytranches(){
          let address = this.$api.dataWeb3.getAddress('DefxCALL', this.contractName);
          address =  address.toLowerCase()
          this.$api.getLastHistorytranches(address).then((data)=> {
             if(data && data.tranches.length >= 1){
                this.item = data.tranches[0]      
              } else {
                this.item = null
              }
         })
    }
  },
  watch: {
    contractName() {
      this.getActivePoolInfo(this.contractName);
    }
  },

  beforeMount() {
       if(/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
           this.isPhone =  true;   //手机端
        } else {
           this.isPhone = false;  //电脑端
        }
  //  (item.withdrawAmount - item.amount) * 365×60×60×24/(item.endTime-item.timestamp) / item.amount
    this.getActivePoolInfo(encodeDefxCallName(poolTypeTabs[0].type, coinsTabs[0].symbol));
        this.getLastHistorytranches();
    // this.getActivePoolInfo(encodeDefxCallName(poolTypeTabs[1].type, coinsTabs[0].symbol));
    // this.getActivePoolInfo(encodeDefxCallName(poolTypeTabs[0].type, coinsTabs[1].symbol));
    // this.getActivePoolInfo(encodeDefxCallName(poolTypeTabs[1].type, coinsTabs[1].symbol));
  }
}
</script>

