<template>
  <div class="w-fill liquidity-slider">
    <div class="fs-18 mt-16">{{language.aoption_balance_rate}}:</div>
    <div class="mt-28 ">
      <m-slider :status="false" shadow disabled class="flex1" v-model="balanceRate" :min="0" :max="100" />
    </div>
    <div class="flex jc-between ai-center mt-28 ">
      <div>
        <div class="">{{language.aoption_your_rate}}</div>
        <div class="mt-8 text">{{balanceRate}}%</div>
      </div>
      <div class="ta-right">
        <div class="">{{language.aoption_pool_size}} {{coin}}</div>
        <div class="mt-8 text">{{totalBalance.toDisplay('balance').toString()}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import mSlider from "@/components/mSlider";
import { BN, BIG_ZERO } from "@/common/util";

export default {
  name: "LiquiditySlider",
  components: {mSlider},
  computed:{
    ...mapGetters(['language']),
    balanceRate() {
      if (this.totalBalance.isEqualTo(0)) {
        return 0
      }
      const amount = new BN(this.amount).toWei('balance')
      const total = this.totalBalance.plus(amount)
      return Math.floor(amount.div(total).times(100).toNumber());
    }
  },
  props: {
    totalBalance: {type: Object, default: BIG_ZERO },
    amount: {type: String, default: ''},
    coin: {type: String, default: ''},
  },
  data(){
    return {
      filterFlag:false,
      searchText:'',
      poolLength:0,
      loading:false,
      balanceRate1:"",
     
    }
  },
  methods:{
